.news-detailed {
    padding-bottom: 60px;
    time {
        display: block;
        margin: 26px 0 15px;
        font-size: 14px;
        font-weight: 600;
        color: var(--Charcoal);
    }
    &__title {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-bottom: 30px;
        border-bottom: 1px dashed var(--Gainsboro);
        margin-bottom: 20px;
        h1 {
            max-width: 850px;
            width: 100%;
            margin: 0 15px 0 0;
            line-height: 1.2;
            flex-shrink: 1;
        }
        .networks {
            display: flex;
            align-items: flex-end;
        }
    }
    img {
        width: calc(50%);
    }
    &__back {
        display: flex;
        align-items: center;
        margin-top: 50px;
        padding-top: 30px;
        border-top: 1px dashed var(--Gainsboro);
        a {
            position: relative;
            display: flex;
            align-items: center;
        }
        i {
            position: absolute;
            transform: rotate(90deg);
            font-size: 6px;
        }
    }
}
@media(max-width: 900px) {
    .news-detailed {
        padding-bottom: 30px;
        &__title {
            flex-direction: column;
            justify-content: flex-start;
            h1 {
                margin-bottom: 30px;
                width: 100%;
            }
        }
        img[align="right"], img[align="left"] {
            width: 100%;
            height: auto;
            margin: 0 0 20px;
        }
        &__back {
            margin-top: 30px;
            padding-top: 30px;
        }
    }
}
@media(max-width: 575px) {
    .news-detailed {
        time {
            margin: 15px 0;
        }
        &__title {
            h1 {
                margin-bottom: 20px;
            }
        }
    }
}